<template>
    <div class="login">
        <top-title></top-title>
        <div class="login-bg" :style="{backgroundImage:`url('/static/user/trial-bg.png')`}">
            <div class="login-box" v-if="typing">
                <h1>请填写资料</h1>
                <table>
                    <tr>
                        <td class="tile">
                            <p>企业名称
                                <span style="width: 100%;display: inline-block"></span>
                            </p>
                        </td>
                        <td class="input">
                            <input type="text" placeholder="请输入企业全称" v-model="form.name">
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">
                            <p>所属行业
                                <span style="width: 100%;display: inline-block"></span>
                            </p>
                        </td>
                        <td class="input">
                            <div class="hack-input al" :class="{focus:set_trade}" @click.self="setTrade()">
                                {{form.trade}}
                                <div class="choose-trade row al" v-show="set_trade">
                                    <div class="col-4 al">
                                        <ul>
                                            <li v-for="(i,k) in industry" :key="k" @click="seletOne(k)">{{i.name}}</li>
                                        </ul>
                                    </div>
                                    <div class="col-4 al">
                                        <ul>
                                            <li v-for="(ii,kk) in industry[selet_one].child" @click="seletTwo(kk) ">
                                                {{ii.name}}
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="col-4 al">
                                        <ul>
                                            <li v-for="(iii,kkk) in industry[selet_one].child[selet_two].child"
                                                @click="insertIndustry(iii)">{{iii}}
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">
                            <p>企业规模
                                <span style="width: 100%;display: inline-block"></span>
                            </p>
                        </td>
                        <td class="input">
                            <input type="text" placeholder="请填写" v-model="form.guimo">
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">
                            <p>职位
                                <span style="width: 100%;display: inline-block"></span>
                            </p>
                        </td>
                        <td class="input">
                            <input type="text" placeholder="请输入您的职位" v-model="form.zhiwei">
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">
                            <p>姓名
                                <span style="width: 100%;display: inline-block"></span>
                            </p>
                        </td>
                        <td class="input">
                            <input type="text" placeholder="请输入您的姓名" v-model="form.username">
                        </td>
                    </tr>
                    <tr>
                        <td class="tile">
                            <p>手机号码
                                <span style="width: 100%;display: inline-block"></span>
                            </p>
                        </td>
                        <td class="input">
                            <input type="text" placeholder="请输入11位手机号码" v-model="form.phone">
                        </td>
                    </tr>
                </table>
                <div class="tips">
                    <span class="ques">请选择您需要的产品</span>
                    <label for="yuangong" class="checkbox " :class="{selected:yuangong}">
                        <input id="yuangong" type="checkbox" name="product" v-model="yuangong">
                        员工福利
                    </label>
                    <label for="shangwu" class="checkbox " :class="{selected:shangwu}">
                        <input id="shangwu" type="checkbox" name="product" v-model="shangwu">
                        商务礼赠
                    </label>
                </div>
                <div class="tips">
                    <span class="ques">您是否需要定制企业专属商城？</span>
                    <label for="mart" class="radio" :class="{selected:mart}">
                        <input id="mart" type="radio" name="mart" @click="mart = true">
                        是
                    </label>
                    <label for="nomart" class="radio" :class="{selected:!mart}">
                        <input id="nomart" type="radio" name="mart" @click="mart = false">
                        否
                    </label>
                </div>
                <div class="tips">
                    <span class="ques">其他您想嘱咐的</span>
                    <label for="notes">
                        <textarea v-model="form.notes" name="notes" id="notes"
                                  placeholder="如果您有其他什么特殊需求，请详细描述"></textarea>
                    </label>
                </div>
                <div class="btn" @click="sub">提交申请</div>
            </div>
            <div class="updated" v-else>
                <img src="./assets/trial-icon-successful.png" alt="">
                <p class="success-msg">
                    您的申请试用信息已提交，请等待反馈
                </p>
                <p class="success-info">
                    您专属的项目顾问会在24小时内联系您，请保持电话畅通。
                    如有其他需求，请拨打客服热线：400-870-3700或咨询在线客服
                </p>
                <a href="/home.html" class="return">
                    返回福分通平台
                </a>
            </div>
            <div class="login-info">
                <p style="margin-bottom: 10px">关于我们 | 福分通商城 | 增值通 | 维修通 | officemate办公伙伴</p>
                <p>版权所有 © CopyRight 2017 欧菲斯办公伙伴控股有限公司 渝ICP备18008342号-2</p>
            </div>
        </div>
    </div>
</template>

<script>
    import TopTitle from '../../../components/TopTile'
    import { industry } from './assets/result'

    export default {
        metaInfo: {
            title: '福分通-申请试用', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        data() {
            return {
                typing: true,
                yuangong: false,
                shangwu: false,
                mart: false,
                set_trade: false,
                form: {
                    phone: '',
                    username: '',
                    trade: '请选择行业',
                    guimo: '',
                    zhiwei: '',
                    name: '',
                    notes: ''
                },
                industry: industry,
                selet_one: 0,
                selet_two: 0,
                selet_three: 0
            }
        },
        components: {
            TopTitle
        },
        methods: {
            sub() {
                for (let i in this.form){
                    if (this.form[i] == ''){
                        if (i==='notes'){
                            continue
                        }
                        alert('请填写字段值')
                        return
                    }
                };
                let product = [];
                let mart = this.mart;
                if (this.yuangong ){
                    product.push('员工福利')
                }
                if(this.shangwu){
                    product.push('商务礼赠')
                }
                let data = Object.assign(this.form,{product:product,mart:mart});
                this.yum('api/Business/businessApply',{apply_data:JSON.stringify(data)}).then(res=>{
                    if (res.data.status === 200){
                        this.typing = false;
                        alert(res.data.msg);
                    }

                })
            },
            setTrade() {
                this.set_trade = !this.set_trade
            },
            seletOne(k) {
                this.selet_two = 0;
                this.selet_three = 0;
                this.selet_one = k;
            },
            seletTwo(k) {
                this.selet_three = 0;
                this.selet_two = k
            },
            insertIndustry(kkk) {
                this.form.trade = kkk;
                this.set_trade = false
            },
        }
    }
</script>

<style lang="stylus">

    .login-bg
        width 100%
        min-width 1200px
        min-height 900px
        height 100%
        position absolute
        top 0
        left 0
        z-index -1
        background-position 50% 0
        background-repeat no-repeat
        background-size 1920px 974px
        background-color black
        text-align center
        .login-box
            display block
            padding 30px 100px
            margin 90px auto 0
            width 700px
            background-color white
            user-select none
            h1
                text-align left
                font-size 24px
                font-weight bold
                margin-bottom 20px
                color black
            table
                width 500px
                border-collapse: separate
                border-spacing 0 20px
                tr
                    height 40px
                    margin-bottom 16px
                    td
                        height 40px
                        line-height 40px
                        &.tile
                            width 80px
                            p
                                text-align justify
                                font-size 16px
                                color black
                                font-weight bold
                                height 40px
                        &.input
                            width 400px
                            text-align right
                            div.hack-input
                                display inline-block
                                width 400px
                                height 40px
                                line-height 40px
                                border-radius 4px
                                border 1px solid #999
                                padding 0 10px
                                user-select none
                                position relative
                                &.focus
                                    box-shadow 0 0 5px #0089ff
                                    border-color #0089ff
                                    &:after
                                        transform rotate(180deg)
                                        transform-origin 50% 20%
                                &::after
                                    display inline-block
                                    content ''
                                    width 0
                                    height 0
                                    border-bottom 5px solid #fff
                                    border-top 5px solid #555
                                    border-left 5px solid #fff
                                    border-right 5px solid #fff
                                    position absolute
                                    right 10px
                                    top 18px
                                .choose-trade
                                    position absolute
                                    width 500px
                                    line-height 30px
                                    background-color white
                                    z-index 1
                                    left 0
                                    top 50px
                                    border 1px solid #999
                                    box-shadow 1px 2px 6px
                                    ul
                                        height 180px
                                        overflow-x hidden
                                        padding 0 10px
                                    li:hover
                                        background-color #c1c1c1
                            input
                                width 400px
                                height 40px
                                border-radius 6px
                                border solid 1px #999999
                                outline none
                                padding 0 10px
                                color #4e4e4e
            .tips
                min-height 40px
                text-align left
                .ques
                    text-align justify
                    font-size 16px
                    color black
                    font-weight bold
                    vertical-align top
                label
                    margin-left 20px
                    font-size 13px
                    color #333
                    input[type=checkbox]
                        display none
                    input[type=radio]
                        display none
                    &.checkbox:before
                        vertical-align text-bottom
                        display inline-block
                        content ''
                        width 16px
                        height 16px
                        border 1px solid #0089ff
                    &.checkbox.selected:before
                        background-image url("./assets/trial-ico-selected.png")
                        background-size 16px 16px
                        background-position center
                    &.radio:before
                        vertical-align text-bottom
                        display inline-block
                        content ''
                        width 16px
                        height 16px
                        border-radius 50%
                        border 1px solid #0089ff
                    &.radio.selected:before
                        background-image url("./assets/trial-ico-selected-2.png")
                        background-size: 17px 17px;
                        background-position-x: -1px;
                        background-position-y: -1px;
                        background-repeat: no-repeat;
                    textarea
                        width 363px
                        height 90px
                        resize none
                        border-radius: 6px;
                        border: solid 1px #999999;
                        outline none
                        padding 15px 10px
                        color #4e4e4e
            .btn
                height 60px
                line-height 60px
                font-size 18px
                color white
                background-color #0089ff
                border-radius 6px
                margin-top 20px
                cursor pointer
                &:hover
                    background-color #35a5ff
        .updated
            width 600px
            margin 150px auto 0
            background-color white
            img
                margin-top 60px
            .success-msg
                font-size 20px
                color black
                font-weight bold
                margin-top 60px
            .success-info
                font-size 14px
                color #555
                text-align center
                width 400px
                line-height 25px
                margin 50px auto 0
            .return
                display inline-block
                width 300px
                height 60px
                line-height 60px
                color white
                font-size 18px
                margin 55px auto
                background-color #0089ff
                border-radius 6px
                &:hover
                    background-color #35a5ff
        .login-info
            width 100%
            min-width 1200px
            position absolute
            bottom 20px
            p
                color white
                user-select none
                a
                    color white
</style>
